:root {
  --primary-color: #030408;
  --secondary-color: #653cf4;
  --text-color: #fff;
}

body {
  box-sizing: border-box;
  background-color: var(--primary-color);
  margin: 0;
  padding: 0;
  font-family: Ubuntu, sans-serif;
  font-style: normal;
  font-weight: 400;
  position: relative;
}

.container {
  min-width: 390px;
  max-width: 1440px;
  height: 100%;
  margin: 0 auto;
  position: relative;
}

.container:before {
  content: "";
  z-index: -1;
  background-image: url("background.74a4179b.svg");
  background-position: 100% 0;
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
}

.notification {
  color: var(--text-color);
  z-index: 9;
  white-space: nowrap;
  background-color: #972b2b;
  border-radius: 5px;
  padding: 15px 30px;
  display: none;
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
}

.success {
  background-color: var(--primary-color);
  color: var(--text-color);
  border: 1px solid var(--text-color);
  z-index: 9;
  white-space: nowrap;
  border-radius: 15px;
  padding: 25px 30px;
  font-size: 22px;
  display: none;
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
}

.footer {
  background-color: var(--text-color);
  border-top-left-radius: 28px;
  border-top-right-radius: 28px;
  flex-direction: column;
  gap: 36px;
  margin-top: 90px;
  padding: 36px;
  display: flex;
}

.nowrap {
  white-space: nowrap;
}

@media (width <= 1168px) {
  .container:before {
    content: "";
    z-index: -1;
    background-image: url("background-mob.19fd45e2.svg");
    background-position: top;
    background-repeat: no-repeat;
    background-size: 100% 30%;
    width: 100%;
    height: 80%;
    display: block;
    position: absolute;
    top: 40px;
    left: 0;
  }
}

@media (width <= 761px) {
  .container:before {
    height: 50%;
  }
}

header {
  border-bottom: 1px solid #191919;
  justify-content: space-between;
  align-items: center;
  padding: 24px 36px 20px;
  display: flex;
}

header img {
  width: 220px;
  height: 38px;
}

header nav {
  gap: 36px;
  display: flex;
}

header nav a {
  color: var(--text-color);
  font-size: 18px;
  line-height: 27px;
  text-decoration: none;
  transition: transform .3s;
}

header nav a:hover {
  transform: translateY(-6px);
}

header .contacts {
  align-items: center;
  gap: 40px;
  display: flex;
}

header .contacts .icons {
  align-items: center;
  gap: 20px;
  display: flex;
}

header .contacts .icons a {
  width: 32px;
  height: 32px;
}

header .contacts .icons a:hover {
  transform: translateY(-4px);
}

header .contacts .icons .tg {
  background-image: url("tg.9ccece0c.svg");
}

header .contacts .icons .vk {
  background-image: url("vk.1a49c021.svg");
  border-radius: 18px;
}

header .contacts .call {
  background-color: var(--text-color);
  color: var(--primary-color);
  border-radius: 28px;
  align-items: center;
  gap: 16px;
  padding: 6px 6px 6px 24px;
  font-size: 20px;
  line-height: 36px;
  text-decoration: none;
  transition: transform .3s;
  display: flex;
}

header .contacts .call .arrow {
  background-image: url("arrow.141a10a2.svg");
  width: 40px;
  height: 40px;
}

header .contacts .call:hover {
  transform: scale(1.1);
}

@media (width <= 1168px) {
  header {
    background-color: var(--primary-color);
  }
}

@media (width <= 885px) {
  header nav {
    display: none;
  }
}

@media (width <= 630px) {
  header {
    padding: 24px 15px 20px;
  }

  header img {
    width: 141px;
    height: 24px;
  }

  header .contacts .icons {
    display: none;
  }

  header .contacts .call {
    padding: 2px 4px 2px 20px;
    font-size: 14px;
    line-height: 19px;
  }

  header .contacts .call .arrow {
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    width: 32px;
    height: 32px;
  }
}

